'use strict';

var angular = require('angular');
require('cerebral-view-angular');

function MenuComponent() {
	return {
		restrict: 'E',
		scope: {},
		template: require('./menu.html'),
		controller: function controller($scope, state, signals) {
			state.inject($scope, {
				counter: ['counter']
			});

			var intervalId = void 0;

			$scope.testStarted = false;
			$scope.startTest = function () {
				if (!$scope.testStarted) {
					intervalId = setInterval(function () {
						signals.nextItemToggled();
					}, 0);
				}
				$scope.testStarted = true;
			};
			$scope.stopTest = function () {
				$scope.testStarted = false;
				clearInterval(intervalId);
			};

			$scope.onCounterClicked = function () {
				signals.counterClicked();
				// debugger;
			};

			$scope.$watch(function () {
				// console.log('$watch')
			});
		}
	};
}

function ListComponent() {
	return {
		restrict: 'E',
		scope: {},
		template: require('./list.html'),
		controller: function controller($scope, state) {
			state.inject($scope, {
				items: ['items']
			});
		}
	};
}

function ItemComponent() {
	return {
		restrict: 'E',
		scope: {
			item: '='
		},
		template: require('./item.html'),
		controller: function controller($scope, state, signals) {
			$scope.onClicked = function (id) {
				signals.itemClicked({ id: id });
			};
		}
	};
}

module.exports.start = function start(controller) {
	angular.module('demo', ['cerebral']).config(function (cerebralProvider) {
		cerebralProvider.setController(controller);
	}).directive('menu', MenuComponent).directive('list', ListComponent).directive('item', ItemComponent);

	angular.bootstrap(document, ['demo']);
};


